:root {
  --footer-color: #243246;
  --main-color-green: #65C466;
  --dark-gray: #414141;
  --background-page: white;
  --background-markers: #E8E2D5;
  --footer-gray: #959AA6;
}
@font-face {
  font-family: "Roboto";
  font-style: italic;
  font-weight: 100;
  src: url("https://fonts.googleapis.com/css2?family=Roboto:ital,wght@1,100&display=swap");
}
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  src: url("https://fonts.googleapis.com/css2?family=Roboto&display=swap");
}
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 500;
  src: url("https://fonts.googleapis.com/css2?family=Roboto:wght@500&display=swap");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
body {
  margin: 0;
  padding-top: 10px;
  background-color: var(--background-page);
  font-family: "Roboto", "Arial", sans-serif;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
main {
  width: 320px;
  margin: 0 auto;
  padding: 0 10px;
}
@media (min-width: 768px) {
  main {
    width: 768px;
    padding: 0 15px;
  }
}
.text-title {
  font-size: 22px;
  font-weight: 700;
  margin: 0 auto;
  display: block;
  text-align: center;
}
@media (min-width: 768px) {
  .text-title {
    font-size: 26px;
  }
}
.text-content {
  font-size: 14px;
  color: var(--dark-gray);
  margin: 10px 0;
}
@media (min-width: 768px) {
  .text-content {
    font-size: 16px;
  }
}
@keyframes img-rotating {
  40% {
    transform: rotate(-7deg);
  }
}
header {
  width: 320px;
  margin: 0 auto;
}
.header {
  width: 300px;
  margin: 0 auto;
  padding: 23px 10px 23px 10px;
  background-color: var(--main-color-green);
  border-radius: 5px;
  margin-bottom: 12px;
}
.header__text {
  font-size: 22px;
  font-weight: 700;
  margin: 0 auto;
  display: block;
  text-align: center;
  color: white;
}
@media (min-width: 768px) {
  .header__text {
    font-size: 26px;
  }
}
.header__text-name {
  color: var(--dark-gray);
}
@media (min-width: 768px) {
  header {
    width: 768px;
  }
  .header {
    width: 768px;
  }
}
.promo__slogan {
  font-size: 16px;
  text-transform: uppercase;
  color: var(--main-color-green);
  margin: 14px 0;
}
.promo {
  margin-bottom: 30px;
  position: relative;
}
.promo__main-photo {
  height: 250px;
  object-fit: cover;
  object-position: 0 -130px;
}
@media (min-width: 768px) {
  .promo__main-photo {
    width: 738px;
    height: auto;
    object-position: 0 0;
  }
  .promo__slogan {
    font-size: 18px;
    width: 500px;
    position: absolute;
    top: 350px;
    left: 50px;
    color: var(--background-page);
    text-shadow: 1px 1px 5px black;
  }
}
.service__title {
  font-size: 22px;
  font-weight: 700;
  margin: 0 auto;
  display: block;
  text-align: center;
  color: var(--main-color-green);
}
@media (min-width: 768px) {
  .service__title {
    font-size: 26px;
  }
}
.service__text {
  font-size: 14px;
  color: var(--dark-gray);
  margin: 10px 0;
  text-align: center;
}
@media (min-width: 768px) {
  .service__text {
    font-size: 16px;
  }
}
.service__carousel {
  margin: 20px auto 40px auto;
}
.carousel__button {
  width: 25px;
  height: 25px;
  border-radius: 50%;
  border: none;
  padding: 0;
  background-color: var(--main-color-green);
  cursor: pointer;
}
.carousel__button-left {
  justify-self: end;
}
.carousel__button-right svg {
  transform: rotate(180deg);
}
.carousel__button:hover {
  box-shadow: 0 1px 1px 0 #cfcfcf, 0 2px 5px 0 #cfcfcf;
}
.carousel__button:active {
  background-color: #44b245;
}
.carousel-rev__button-right svg {
  transform: rotate(180deg);
}
.carousel-rev__button-left {
  justify-self: end;
}
.carousel {
  display: grid;
  grid-template-columns: auto auto;
  grid-template-rows: repeat(3, auto);
  column-gap: 30px;
}
.carousel__view {
  grid-column-start: 1;
  grid-column-end: 3;
  margin: 0 auto;
  width: 220px;
  height: 295px;
  border-radius: 8px;
  box-shadow: 0 0 25px rgba(0, 0, 0, 0.2);
  overflow: hidden;
  position: relative;
  display: flex;
}
@media (min-width: 768px) {
  .carousel__view {
    width: 690px;
    height: 350px;
    box-shadow: none;
    align-items: end;
  }
}
.list-cards {
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;
  width: calc(230*8px);
  transition: all 1s;
  position: absolute;
  left: 0;
}
@media (min-width: 768px) {
  .list-cards {
    align-items: flex-end;
  }
}
.list-cards__item {
  box-sizing: border-box;
  width: 220px;
  height: 295px;
  margin-right: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  transition: all 800ms;
}
@media (min-width: 768px) {
  .list-cards__item {
    box-shadow: 0 0 10px 1px rgba(0, 0, 0, 0.3);
    margin-bottom: 10px;
    width: 190px;
    margin-right: 20px;
    margin-left: 20px;
    border-radius: 8px;
  }
}
@media (min-width: 768px) {
  .list-cards__item-current.list-cards__item {
    box-shadow: 0 0 20px 2px rgba(88, 170, 89, 0.5);
    margin-bottom: 10px;
    width: 210px;
    margin-right: 10px;
    margin-left: 10px;
    height: 330px;
  }
}
.list-cards__img {
  display: block;
  margin-top: 20px;
  object-fit: cover;
  border-radius: 50%;
  width: 130px;
  height: 130px;
  box-shadow: 0 0 0 5px white, 0 0 0 10px var(--main-color-green);
}
.list-cards__title {
  font-size: 18px;
  font-weight: 500;
  text-align: center;
  color: var(--main-color-green);
  margin-bottom: 3px;
  width: 180px;
}
.list-cards__text {
  margin: 3px 0;
  text-align: center;
  font-size: 13px;
  color: var(--dark-gray);
  font-weight: 500;
  width: 180px;
}
.list-cards__item-current .list-cards__img {
  animation-name: img-rotating;
  animation-duration: 800ms;
  animation-delay: 200ms;
  animation-timing-function: ease-out;
}
.markers {
  grid-column-start: 1;
  grid-column-end: 3;
  width: 210px;
  margin: 20px auto;
}
.markers__list {
  list-style: none;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 5px;
  overflow: hidden;
  position: relative;
  background-color: var(--background-markers);
  display: flex;
  justify-content: space-between;
}
.markers__item-service {
  width: 23px;
  height: 4px;
  border-radius: 7px;
  box-shadow: 0 0 0 3.3px var(--background-page);
  z-index: 2;
}
.markers__fill-service {
  width: 24px;
  height: 5px;
  position: absolute;
  left: 0;
  top: -0.5px;
  background-color: var(--main-color-green);
  transition: all 1s;
  border-radius: 10px;
}
.about-self {
  margin: 30px auto;
}
.about-self__title {
  font-size: 22px;
  font-weight: 700;
  margin: 0 auto;
  display: block;
  text-align: center;
  color: var(--main-color-green);
}
@media (min-width: 768px) {
  .about-self__title {
    font-size: 26px;
  }
}
.about-self__text {
  font-size: 14px;
  color: var(--dark-gray);
  margin: 10px 0;
  text-align: justify;
  word-break: break-word;
}
@media (min-width: 768px) {
  .about-self__text {
    font-size: 16px;
  }
}
@media (min-width: 768px) {
  .about-self__text {
    margin: 20px 0;
  }
}
@media (min-width: 768px) {
  .about-self__text::before {
    content: '';
    width: 400px;
    height: 225px;
    background-image: url("../img/promo1.jpg");
    background-position: -180px -210px;
    float: right;
    margin-left: 25px;
  }
}
.about-self__contacts {
  margin-top: 8px;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 14px;
  color: var(--dark-gray);
  margin: 10px 0;
}
@media (min-width: 768px) {
  .about-self__contacts {
    font-size: 16px;
  }
}
.about-self__contacts a {
  color: var(--dark-gray);
  text-decoration: none;
}
.about-self__contacts a:hover {
  color: var(--main-color-green);
}
.about-self__tel {
  margin-bottom: 8px;
}
.form-ask {
  width: 266px;
  margin: 20px auto 40px auto;
}
@media (min-width: 768px) {
  .form-ask {
    width: 640px;
    display: grid;
    grid-template-columns: 320px 320px;
    grid-template-rows: auto 45px auto auto;
  }
}
.form-ask__title {
  font-size: 22px;
  font-weight: 700;
  margin: 0 auto;
  display: block;
  text-align: center;
  color: var(--main-color-green);
  margin-bottom: 15px;
}
@media (min-width: 768px) {
  .form-ask__title {
    font-size: 26px;
  }
}
@media (min-width: 768px) {
  .form-ask__title {
    grid-column-start: 1;
    grid-column-end: 3;
    margin-bottom: 20px;
  }
}
.form-ask__img {
  object-fit: cover;
  object-position: -20px -70px;
  display: block;
  margin: 0 auto 20px auto;
}
@media (min-width: 768px) {
  .form-ask__img {
    grid-row-start: 2;
    grid-row-end: 5;
    margin: 0 auto;
  }
}
.form-ask__email {
  border: 0.8px solid #CCCCCC;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
  border-radius: 4px;
  width: 266px;
  height: 33px;
  margin: 0 auto 11px auto;
  display: block;
  padding: 10px;
}
@media (min-width: 768px) {
  .form-ask__email {
    width: 320px;
  }
}
.form-ask__email::placeholder {
  font-size: 11px;
  line-height: 13px;
  color: #C2C2C2;
}
@media (min-width: 768px) {
  .form-ask__email::placeholder {
    font-size: 13px;
    line-height: 16px;
  }
}
.form-ask__question {
  outline: none;
  border: 0.8px solid #CCCCCC;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
  border-radius: 4px;
  resize: none;
  width: 266px;
  height: 90px;
  margin: 0 auto 11px auto;
  display: block;
  padding: 10px;
}
@media (min-width: 768px) {
  .form-ask__question {
    width: 320px;
    height: 150px;
  }
}
.form-ask__question::placeholder {
  font-family: "Roboto", "Arial", sans-serif;
  font-weight: 400;
  font-size: 11px;
  line-height: 13px;
  color: #C2C2C2;
}
@media (min-width: 768px) {
  .form-ask__question::placeholder {
    font-size: 13px;
    line-height: 16px;
  }
}
.form-ask__wrapper {
  display: flex;
  align-items: center;
}
.form-ask__notice {
  display: block;
  width: 152px;
  font-size: 8px;
  line-height: 10px;
  color: #A3A3A3;
  position: relative;
  padding-left: 22px;
}
@media (min-width: 768px) {
  .form-ask__notice {
    font-size: 10px;
    line-height: 13px;
    width: 182px;
  }
}
.form-ask__notice::before {
  content: '';
  width: 12px;
  height: 12px;
  border: 0.7px solid var(--main-color-green);
  border-radius: 3.4px;
  display: block;
  position: absolute;
  top: 9px;
  left: 0;
  cursor: pointer;
}
.form-ask__checkbox:checked + .form-ask__notice::after {
  content: '';
  width: 8px;
  height: 8px;
  background-color: var(--main-color-green);
  border-radius: 2px;
  display: block;
  position: absolute;
  top: 10.9px;
  left: 2.1px;
  cursor: pointer;
}
@media (min-width: 768px) {
  .form-ask__checkbox:checked + .form-ask__notice::after {
    top: 11px;
    left: 2.1px;
  }
}
.form-ask__submit {
  width: 103px;
  height: 23px;
  margin-left: auto;
  background: var(--main-color-green);
  box-shadow: 0px 2.2px 8.3px rgba(0, 0, 0, 0.25);
  border-radius: 2.8px;
  border: none;
  color: var(--background-page);
}
.form-ask__submit:hover {
  box-shadow: none;
}
.form-ask__submit:active {
  background-color: #44b245;
}
.reviews {
  margin: 20px auto 40px auto;
}
.item-rev {
  padding: 15px;
}
.item__rating {
  height: 15px;
}
.reviews__title {
  font-size: 22px;
  font-weight: 700;
  margin: 0 auto;
  display: block;
  text-align: center;
  color: var(--main-color-green);
}
@media (min-width: 768px) {
  .reviews__title {
    font-size: 26px;
  }
}
.item__text-rev {
  margin: 12px 0;
  font-size: 13px;
  color: #696969;
  font-weight: 500;
}
@media (min-width: 768px) {
  .item__text-rev {
    font-size: 15px;
  }
}
.item__author-avatar {
  width: 37px;
  height: 37px;
  border-radius: 50%;
  object-fit: cover;
  float: left;
  margin-right: 15px;
  margin-top: -3px;
}
@media (min-width: 768px) {
  .item__author-avatar {
    width: 40px;
    height: 40px;
  }
}
.item__author-name {
  margin: 5px 0;
  font-size: 12px;
  color: var(--dark-gray);
  font-weight: 500;
}
@media (min-width: 768px) {
  .item__author-name {
    font-size: 14px;
  }
}
.item__author-company {
  margin: 5px 0;
  font-size: 9px;
  color: var(--main-color-green);
  font-weight: 100;
  font-style: italic;
}
@media (min-width: 768px) {
  .item__author-company {
    font-size: 12px;
  }
}
.carousel-rev {
  width: auto;
  margin: 10px auto 0 auto;
  display: grid;
  grid-template-columns: auto auto;
  grid-template-rows: repeat(3, auto);
  column-gap: 30px;
}
@media (min-width: 768px) {
  .carousel-rev {
    width: 640px;
  }
}
.carousel-rev__view {
  grid-column-start: 1;
  grid-column-end: 3;
  width: auto;
  height: 167px;
  box-shadow: inset 0 0 12px rgba(0, 0, 0, 0.1);
  border-radius: 6px;
  overflow: hidden;
  position: relative;
}
@media (min-width: 768px) {
  .carousel-rev__view {
    height: 177px;
  }
}
.carousel-rev__list {
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;
  width: calc(310*4px);
  transition: all 1s;
  position: absolute;
  left: 0;
}
@media (min-width: 768px) {
  .carousel-rev__list {
    width: calc(320*4px);
  }
}
.carousel-rev__item {
  box-sizing: border-box;
  width: 300px;
  margin-right: 10px;
}
@media (min-width: 768px) {
  .carousel-rev__item {
    width: 310px;
    margin-right: 5px;
    margin-left: 5px;
  }
}
.markers__item {
  width: 49px;
  height: 4px;
  border-radius: 10px;
  box-shadow: 0 0 0 3.5px var(--background-page);
  z-index: 2;
}
.markers__fill {
  width: 24px;
  height: 5px;
  position: absolute;
  left: 0;
  top: -0.5px;
  background-color: var(--main-color-green);
  transition: all 1s;
  border-radius: 10px;
  width: 53px;
}
footer {
  background-color: var(--footer-color);
}
.footer {
  width: 320px;
  margin: 0 auto;
  padding: 13px;
}
@media (min-width: 768px) {
  .footer {
    width: 765px;
    display: flex;
  }
}
.footer__contacts {
  font-size: 14px;
  font-style: italic;
  font-weight: 100;
}
@media (min-width: 768px) {
  .footer__contacts {
    font-size: 16px;
  }
}
.footer__contacts a {
  text-decoration: none;
  margin-left: 14px;
  color: var(--footer-gray);
}
.footer__tel,
.footer__email {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}
.footer__tel-number,
.footer__email-link {
  order: 2;
}
.footer__tel-number:hover,
.footer__email-link:hover {
  color: var(--background-page);
}
.footer__tel-number:hover + svg {
  fill: white;
}
.footer__email-link:hover + svg {
  fill: white;
}
.footer__copyright {
  font-size: 12px;
  color: var(--footer-gray);
  text-align: center;
  margin: 5px auto;
}
@media (min-width: 768px) {
  .footer__copyright {
    font-size: 14px;
    margin: 0 0 0 auto;
  }
}
.footer__copyright-city,
.footer__copyright-text {
  margin: 0 auto 5px auto;
}
.footer__copyright-city {
  color: var(--background-page);
}
@media (min-width: 768px) {
  .footer__copyright-city {
    margin-bottom: 12px;
  }
}
